import React from 'react';
import { default as MuiPopover, PopoverProps } from '@mui/material/Popover';

const Popover = React.forwardRef<HTMLElement, PopoverProps>( ( props, ref ) => {
	return (
		<MuiPopover
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<PopoverProps>;

export default Popover;
